import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniSearchBar,
		uniLoadMore
	},
	data() {
		return {
			listData: [],
			reload: false,
			query: {
				currPage: 0,
				rows: 10,
				title: '',
				endCreatedTime: '',
			},
			totalPage: 0,
			status: 'more',
		}
	},
	onLoad() {
		this.query.endCreatedTime = this.getTimeNow();
		this.getList();
	},
	onPullDownRefresh() {
		this.query.title = '';
		this.query.currPage= 0;
		this.query.endCreatedTime = this.getTimeNow();
		this.reload = true;
		this.getList();
		uni.stopPullDownRefresh()
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		// 获取当前时间
		getTimeNow(){
			let d = new Date()
			let format = d.getFullYear() + '-' + 
						(d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1) + '-' + 
						(d.getDate()<10 ? "0" +d.getDate():d.getDate())+ ' ' + 
						(d.getHours()<10 ?"0"+d.getHours():d.getHours()) + ':' + 
						(d.getMinutes()<10 ? "0" + d.getMinutes() : d.getMinutes() ) + ':' + 
						(d.getSeconds()<10 ? "0" + d.getSeconds() : d.getSeconds() );
			return format
		},
		search(){
			this.query.currPage= 0;
			this.reload = true;
			this.getList();
		},
		async getList() {
			if (this.query.currPage != 0 && this.query.currPage == this.totalPage) return;
			this.query.currPage++;
			
			this.status = 'loading'
			let ret = await this.$http({
				url: '/favorites',
				data: this.query
			});
			this.listData = this.reload ? ret.data.dataList: this.listData.concat(ret.data.dataList)
			this.reload = false;
			this.totalPage = ret.data.totalPage
			// 判断是否没有数据
			if (this.totalPage == this.query.currPage) this.status = 'noMore'
			else this.status = 'more'
		},
		getDetail (e) {
			this.$http({url: "/article/addNum", data: {id: e.id}})
			uni.navigateTo({
				url: "/pages/details/details?id=" + e.id
			})
		},
	}
}
